import ToggleButton from "react-bootstrap/ToggleButton";
import ToggleButtonGroup from "react-bootstrap/ToggleButtonGroup";

import {
  LyricsTransformType,
  LineTransformType,
  VerseTransformType,
} from "../types";
import { useState } from "react";

export const useLyricsTransformType = (): [
  LyricsTransformType,
  (t: LyricsTransformType) => void
] => {
  const [lyricsTransformType, setLyricsTransformType] =
    useState<LyricsTransformType>({
      lineTransformType: LineTransformType.Full,
      verseTransformType: VerseTransformType.Full,
    });

  return [lyricsTransformType, setLyricsTransformType];
};

type Props = {
  setLyricsTransformType: (t: LyricsTransformType) => void;
  lyricsTransformType: LyricsTransformType;
};

export const LyricsTransformSelector = ({
  setLyricsTransformType,
  lyricsTransformType,
}: Props): JSX.Element => {
  const { lineTransformType, verseTransformType } = lyricsTransformType;

  const updateLineTransformType = (lineTransformType: LineTransformType) =>
    setLyricsTransformType({ ...lyricsTransformType, lineTransformType });
  const updateVerseTransformType = (verseTransformType: VerseTransformType) =>
    setLyricsTransformType({ ...lyricsTransformType, verseTransformType });

  return (
    <>
      <ToggleButtonGroup
        name="line-type"
        type="radio"
        value={lineTransformType}
        onChange={updateLineTransformType}
      >
        <ToggleButton
          value={LineTransformType.Full}
          id="full-line"
          variant="secondary"
        >
          Full Line
        </ToggleButton>
        <ToggleButton
          value={LineTransformType.FirstLetterOnly}
          id="first-letter"
          variant="secondary"
        >
          First Letter Only
        </ToggleButton>
        <ToggleButton
          value={LineTransformType.FirstWordOnly}
          id="first-word"
          variant="secondary"
        >
          First Word Only
        </ToggleButton>
      </ToggleButtonGroup>
      <ToggleButtonGroup
        name="verse-type"
        type="radio"
        value={verseTransformType}
        onChange={updateVerseTransformType}
      >
        <ToggleButton
          value={VerseTransformType.Full}
          id="full-verese"
          variant="secondary"
        >
          Full Verse
        </ToggleButton>
        <ToggleButton
          value={VerseTransformType.FirstLineOnly}
          id="first-line"
          variant="secondary"
        >
          First Line Only
        </ToggleButton>
      </ToggleButtonGroup>
    </>
  );
};
