import { Line, LineTransformType, Verse, VerseTransformType } from "./types";

export const LineTransformFunctions = {
  [LineTransformType.Full]: (line: Line): React.ReactElement => <>{line}</>,
  [LineTransformType.FirstWordOnly]: (line: Line) => (
    <>
      {line.split(/\s(.*)/).map((word: string, index: number) => (
        <>
          <span className={index === 0 ? "show" : "hide"}>{word}</span>{" "}
        </>
      ))}
    </>
  ),
  [LineTransformType.FirstLetterOnly]: (line: Line) => (
    <>
      {line.split(/([a-zA-Z']+)/).map((word: string, index: number) => {
        if (!(index % 2)) {
          return word;
        }

        return (
          <>
            {word.substring(0, 1)}
            <span className="hide">{word.substring(1)}</span>
          </>
        );
      })}
    </>
  ),
};

export const VerseTransformFunctions = {
  [VerseTransformType.Full]: (
    lineTransformFunction: (line: Line) => React.ReactElement,
    verse: Verse
  ) => verse.map((line) => <div>{lineTransformFunction(line)}</div>),
  [VerseTransformType.FirstLineOnly]: (
    lineTransformFunction: (line: Line) => React.ReactElement,
    verse: Verse
  ) =>
    verse.map((line, index) => (
      <div>
        <span className={index === 0 ? "show" : "hide"}>
          {index === 0 ? lineTransformFunction(line) : line}
        </span>
      </div>
    )),
};
