import { useState } from "react";

import { useLocalStorage } from "../hooks/useLocalStorage";

import {
  LyricsTransformSelector,
  useLyricsTransformType,
} from "../components/LyricsTransformSelector";
import { LyricsOutput } from "../components/LyricsOutput";
import { Button, Container, Navbar, Form } from "react-bootstrap";
import { Lyrics } from "../types";

const VERSE_SEPERATOR_PATTERN = /\r\n\r\n|\r\r|\n\n/;
const LINE_SEPERATOR_PATTERN = /\r\n|\r|\n/;

const TextInputRoute = (): JSX.Element => {
  const [rawText, setRawText] = useLocalStorage("raw-text", "");
  const [song, setSong] = useState<Lyrics | null>(null);
  const [lyricsTransformType, setLyricsTransformType] =
    useLyricsTransformType();

  const parseAndSetSong = () =>
    setSong(
      rawText
        .split(VERSE_SEPERATOR_PATTERN)
        .map((chunk) => chunk.split(LINE_SEPERATOR_PATTERN))
    );

  return (
    <>
      <Navbar className="bg-body-tertiart">
        <Container className="control-bar">
          <LyricsTransformSelector
            lyricsTransformType={lyricsTransformType}
            setLyricsTransformType={setLyricsTransformType}
          />
        </Container>
      </Navbar>
      {!song && (
        <Container>
          <Form.Control
            as="textarea"
            className="w-100 mb-3"
            value={rawText}
            onChange={(e) => setRawText(String(e.target.value))}
            rows={30}
          />
          <Button type="submit" onClick={parseAndSetSong}>
            Submit
          </Button>
        </Container>
      )}
      {song && (
        <Container>
          <LyricsOutput
            lyrics={song}
            lyricsTransformType={lyricsTransformType}
          />
          <Button type="submit" className="mt-3" onClick={() => setSong(null)}>
            Reset
          </Button>
        </Container>
      )}
    </>
  );
};

export default TextInputRoute;
