import { Card } from "react-bootstrap";

import { Lyrics, LyricsTransformType } from "../types";

import {
  VerseTransformFunctions,
  LineTransformFunctions,
} from "../TransformFunctions";

type Props = {
  lyrics: Lyrics | null;
  lyricsTransformType: LyricsTransformType;
};

export const LyricsOutput = ({
  lyrics,
  lyricsTransformType,
}: Props): JSX.Element => {
  if (!lyrics?.length) {
    return (
      <Card>
        <Card.Body>
          <Card.Text className="serifed text-center">
            Lyrics not yet provided for this section.
          </Card.Text>
        </Card.Body>
      </Card>
    );
  }

  const { lineTransformType, verseTransformType } = lyricsTransformType;

  return (
    <Card>
      <Card.Body>
        <Card.Text className="serifed text-center sm-display-2 lg-display-5">
          {lyrics.map((verse, index) => {
            return (
              <p key={index}>
                {VerseTransformFunctions[verseTransformType](
                  LineTransformFunctions[lineTransformType],
                  verse
                )}
              </p>
            );
          })}
        </Card.Text>
      </Card.Body>
    </Card>
  );
};
