export type LyricsTransformType = {
  lineTransformType: LineTransformType;
  verseTransformType: VerseTransformType;
};

export enum LineTransformType {
  Full,
  FirstLetterOnly,
  FirstWordOnly,
}
export enum VerseTransformType {
  Full,
  FirstLineOnly,
}

export type Line = string;
export type Verse = Line[];
export type Lyrics = Verse[];

export type Song = {
  title: string;
  lyrics: { [section in Section]?: Lyrics };
  loungeLink: string;
};

export enum Section {
  Tenor_1 = "Tenor_1",
  Tenor_2 = "Tenor_2",
  Baritone = "Baritone",
  Bass = "Bass",
}
